.gpt3__cta {
  /* max-width: 1170px; */

  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  border: 1px solid #000000;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10.7236px;
}

.gpt3__cta {
  display: flex;
  flex-direction: row;
  padding: 2rem;
}

.gpt3__cta-content {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.gpt3__cta-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
  color: #000000;
}

.gpt3__cta-content p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: #0e0e0e;
}

.gpt3__cta button {
  width: 189.14px;
  height: 59.4px;
  background: #000000;
  border-radius: 40px;
  font-family: var(--font-family);
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  align-self: center;
}

@media screen and (max-width: 900px) {
  .gpt3__cta {
    flex-direction: column;
  }

  .gpt3__cta-content {
    align-items: center;
    justify-content: center;
    margin-right: 0;
  }

  .gpt3__cta-content h1 {
    text-align: center;
    font-size: 20px;
    line-height: 36px;
  }
  .gpt3__cta button {
    margin-top: 1rem;
    font-size: 16px;
    width: 160px;
    height: 48px;
  }
}
