.gpt3__features {
  display: flex;
  /* justify-content: center;
  align-items: flex-start; */
  overflow: unset;
  margin-top: 6rem;
}

.gpt3__features-heading {
  flex: 1;
  margin-top: 1.2rem;
}
.gpt3__features-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.gpt3__features-heading p {
  font-family: var(--font-family);

  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  margin-top: 1rem;
  cursor: pointer;
  color: #ff8a71;
}

.gpt3__features-container {
  flex: 1;
}

@media screen and (max-width: 900px) {
  .gpt3__features {
    flex-direction: column;
  }

  .gpt3__features .gpt3__features-container__feature {
    margin: 2rem 0;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__features {
    margin-top: 1rem;
  }
  .gpt3__features-heading h1 {
    font-size: 24px;
    line-height: 36px;
  }
  .gpt3__features .gpt3__features-container__feature {
    flex-direction: column;
  }

  .gpt3__features .gpt3__features-container__feature-text {
    margin-top: 0.9rem;
  }
}
