.gpt3__possibility {
  display: flex;
  flex-direction: row;
}

.gpt3__possibility-image {
  flex: 1;
  justify-content: center;
  align-items: center;
}

.gpt3__possibility-image img {
  width: 100%;
  height: 100%;
}

.gpt3__possibility-content {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 5rem;
}

.gpt3__possibility-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}

.gpt3__possibility-content p {
  font-family: var(--font-family);

  font-weight: normal;
  font-size: 16px;
  line-height: 30px;

  color: #81afdd;
  margin: 1rem 0;
}

.gpt3__possibility-content_subtext-1 {
  color: #71e5ff;
}

.gpt3__possibility-content_subtext-2 {
  color: #ff8a71;
}

.subtext {
  margin: 0 !important;
}

@media screen and (max-width: 900px) {
  .gpt3__possibility {
    flex-direction: column;
  }
  .gpt3__possibility-content {
    margin: 2rem auto;
  }
}
