.gpt3__brands {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0px;
  margin: 4rem 6rem;
}

.gpt3__brands div {
  margin: 0 1rem;
}

@media screen and (max-width: 650px) {
  .gpt3__brands {
    flex-direction: column;
    margin: 1rem 3rem;
  }
  .gpt3__brands div {
    margin: 0.5rem 0;
  }
}
